import { motion } from "framer-motion";
import { Clock, CheckCircle2, XCircle, TrendingUp } from "lucide-react";
import { StatCard } from "./StatCard";
import { TaskStatsCard } from "./TaskStatsCard";
interface TaskDefinition {
  id: string;
  text: string;
  description?: string;
  baselineDuration: number;
  isRecurring: boolean;
  recurringDays: number[];
}
interface TaskAssignment {
  id: string;
  taskId: string;
  dateStr: string;
  durationOverride?: number;
  completed: boolean;
}
interface StatsPanelProps {
  taskDefinitions: TaskDefinition[];
  assignments: TaskAssignment[];
  daysOff: string[];
}
export function StatsPanel({
  taskDefinitions,
  assignments,
  daysOff,
}: StatsPanelProps) {
  // Filter assignments to only include dates from January 2026 onwards
  const STATS_START_DATE = "2026-01-01";
  const filteredAssignments = assignments.filter(
    (a) => a.dateStr >= STATS_START_DATE,
  );
  // Calculate global statistics
  const calculateGlobalStats = () => {
    // Total time spent (completed tasks only)
    const totalMinutes = filteredAssignments
      .filter((a) => a.completed)
      .reduce((sum, a) => {
        const def = taskDefinitions.find((d) => d.id === a.taskId);
        if (!def) return sum;
        const duration = a.durationOverride || def.baselineDuration;
        return sum + duration;
      }, 0);
    const totalHours = Math.round((totalMinutes / 60) * 10) / 10;
    // Group assignments by date
    const dateMap = new Map<string, TaskAssignment[]>();
    filteredAssignments.forEach((a) => {
      if (!dateMap.has(a.dateStr)) {
        dateMap.set(a.dateStr, []);
      }
      dateMap.get(a.dateStr)!.push(a);
    });
    // Count completed and missed days (excluding off-days)
    let completedDays = 0;
    let missedDays = 0;
    dateMap.forEach((dayAssignments, dateStr) => {
      // Skip off-days
      if (daysOff.includes(dateStr)) return;
      const allCompleted =
        dayAssignments.length > 0 && dayAssignments.every((a) => a.completed);
      const anyIncomplete = dayAssignments.some((a) => !a.completed);
      if (allCompleted) {
        completedDays++;
      } else if (anyIncomplete) {
        missedDays++;
      }
    });
    return {
      totalHours,
      completedDays,
      missedDays,
    };
  };
  // Calculate per-task statistics
  const calculateTaskStats = (taskId: string) => {
    const taskAssignments = filteredAssignments.filter(
      (a) => a.taskId === taskId,
    );
    const def = taskDefinitions.find((d) => d.id === taskId);
    if (!def) return null;
    // Total time spent (completed only)
    const totalMinutes = taskAssignments
      .filter((a) => a.completed)
      .reduce((sum, a) => {
        const duration = a.durationOverride || def.baselineDuration;
        return sum + duration;
      }, 0);
    const totalHours = Math.round((totalMinutes / 60) * 10) / 10;
    // Count completed and missed (excluding off-days)
    const completed = taskAssignments.filter(
      (a) => a.completed && !daysOff.includes(a.dateStr),
    ).length;
    const missed = taskAssignments.filter(
      (a) => !a.completed && !daysOff.includes(a.dateStr),
    ).length;
    return {
      taskName: def.text,
      description: def.description,
      totalHours,
      completedCount: completed,
      missedCount: missed,
      isRecurring: def.isRecurring,
      recurringDays: def.recurringDays,
    };
  };
  const globalStats = calculateGlobalStats();
  const taskStats = taskDefinitions
    .map((def) => calculateTaskStats(def.id))
    .filter((stat) => stat !== null);
  return (
    <motion.div
      initial={{
        opacity: 0,
      }}
      animate={{
        opacity: 1,
      }}
      className="w-full space-y-8"
    >
      {/* Header */}
      <div className="text-center">
        <h2 className="font-handwriting text-4xl text-[#2C2416] mb-2">
          Performance Overview
        </h2>
        <p className="font-serif-text text-sm text-[#8B7355] italic">
          Statistics from January 2026 onwards
        </p>
        <div className="h-px w-32 mx-auto bg-[#E8DCC4] mt-2" />
      </div>

      {/* Global Statistics */}
      <div>
        <div className="flex items-center gap-2 mb-4">
          <TrendingUp className="w-5 h-5 text-[#8B7355]" />
          <h3 className="font-serif-text text-lg font-bold text-[#8B7355] uppercase tracking-wider">
            Overall Statistics
          </h3>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <StatCard
            label="Total Time"
            value={`${globalStats.totalHours}h`}
            icon={Clock}
            subtitle="Across all completed tasks"
            index={0}
          />
          <StatCard
            label="Completed Days"
            value={globalStats.completedDays}
            icon={CheckCircle2}
            subtitle="All tasks done"
            index={1}
          />
          <StatCard
            label="Missed Days"
            value={globalStats.missedDays}
            icon={XCircle}
            subtitle="Some tasks incomplete"
            index={2}
          />
        </div>
      </div>

      {/* Per-Task Statistics */}
      <div>
        <div className="flex items-center gap-2 mb-4">
          <TrendingUp className="w-5 h-5 text-[#8B7355]" />
          <h3 className="font-serif-text text-lg font-bold text-[#8B7355] uppercase tracking-wider">
            Task Breakdown
          </h3>
        </div>

        {taskStats.length === 0 ? (
          <div className="text-center py-12 bg-white rounded-lg border border-[#E8DCC4]">
            <p className="font-handwriting text-2xl text-[#8B7355]">
              No task data yet...
            </p>
            <p className="font-serif-text text-sm text-[#8B7355] mt-2">
              Complete some tasks to see statistics!
            </p>
          </div>
        ) : (
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-4">
            {taskStats.map((stat, index) => (
              <TaskStatsCard
                key={index}
                taskName={stat!.taskName}
                description={stat!.description}
                totalHours={stat!.totalHours}
                completedCount={stat!.completedCount}
                missedCount={stat!.missedCount}
                isRecurring={stat!.isRecurring}
                recurringDays={stat!.recurringDays}
                index={index}
              />
            ))}
          </div>
        )}
      </div>
    </motion.div>
  );
}
