import { useEffect, useState } from "react";
import { ChevronLeft, ChevronRight, PenTool } from "lucide-react";
import { WeekView } from "../components/WeekView";
import { MonthCalendar } from "../components/MonthCalendar";
import { ViewToggle } from "../components/ViewToggle";
import { CompleteDayButton } from "../components/CompleteDayButton";
import { TaskCreationModal } from "../components/TaskCreationModal";
import { TaskEditModal } from "../components/TaskEditModal";
import { DeleteConfirmModal } from "../components/DeleteConfirmModal";
import { TaskLibrary } from "../components/TaskLibrary";
import { TaskAssignmentModal } from "../components/TaskAssignmentModal";
import { BulkActionsBar } from "../components/BulkActionsBar";
import { DayData, Task } from "../components/DayCard";
import { StatsPanel } from "../components/StatsPanel";
import { useTasksAPI, type TaskDefinition } from "../hooks/useTasksAPI";
import { useAssignmentsAPI, type TaskAssignment } from "../hooks/useAssignmentsAPI";
import { useDaysOffAPI } from "../hooks/useDaysOffAPI";
// Helper to get local date string (fixes timezone offset bug)
const getLocalDateStr = (date: Date): string => {
  const year = date.getFullYear();
  const month = String(date.getMonth() + 1).padStart(2, "0");
  const day = String(date.getDate()).padStart(2, "0");
  return `${year}-${month}-${day}`;
};
export function JournalTracker() {
  const [currentDate, setCurrentDate] = useState(new Date());
  const [view, setView] = useState<"week" | "month" | "stats">("week");
  // Modals state
  const [isCreateModalOpen, setIsCreateModalOpen] = useState(false);
  const [editingTask, setEditingTask] = useState<TaskDefinition | null>(null);
  const [deletingTask, setDeletingTask] = useState<TaskDefinition | null>(null);
  const [assignModalTask, setAssignModalTask] = useState<TaskDefinition | null>(
    null,
  );
  // Selection state
  const [isSelectMode, setIsSelectMode] = useState(false);
  const [selectedDays, setSelectedDays] = useState<string[]>([]);
  // Data State
  const [taskDefinitions, setTaskDefinitions] = useState<TaskDefinition[]>([
    {
      id: "1",
      text: "Morning standup",
      baselineDuration: 15,
      isRecurring: true,
      recurringDays: [1, 2, 3, 4, 5],
    },
    {
      id: "2",
      text: "Deep Work Session",
      baselineDuration: 90,
      isRecurring: false,
      recurringDays: [],
    },
    {
      id: "3",
      text: "Check Emails",
      baselineDuration: 30,
      isRecurring: true,
      recurringDays: [1, 2, 3, 4, 5],
    },
  ]);
  const [assignments, setAssignments] = useState<TaskAssignment[]>([]);
  const [daysOff, setDaysOff] = useState<string[]>([]);
  // Recurring Task Logic: Auto-generate assignments for view range
  useEffect(() => {
    const recurringDefs = taskDefinitions.filter((d) => d.isRecurring);
    if (recurringDefs.length === 0) return;
    const newAssignments: TaskAssignment[] = [];
    // Generate for current month +/- 1 month to cover all views
    const start = new Date(
      currentDate.getFullYear(),
      currentDate.getMonth() - 1,
      1,
    );
    const end = new Date(
      currentDate.getFullYear(),
      currentDate.getMonth() + 2,
      0,
    );
    for (let d = new Date(start); d <= end; d.setDate(d.getDate() + 1)) {
      const dateStr = getLocalDateStr(d); // Use local date helper
      const dayOfWeek = d.getDay();
      recurringDefs.forEach((def) => {
        if (def.recurringDays.includes(dayOfWeek)) {
          const exists = assignments.some(
            (a) => a.taskId === def.id && a.dateStr === dateStr,
          );
          const newExists = newAssignments.some(
            (a) => a.taskId === def.id && a.dateStr === dateStr,
          );
          if (!exists && !newExists) {
            newAssignments.push({
              id: `auto-${def.id}-${dateStr}`,
              taskId: def.id,
              dateStr,
              completed: false,
            });
          }
        }
      });
    }
    if (newAssignments.length > 0) {
      setAssignments((prev) => [...prev, ...newAssignments]);
    }
  }, [currentDate, taskDefinitions, assignments]);
  // Helper to get tasks for a specific date
  const getTasksForDate = (date: Date): Task[] => {
    const dateStr = getLocalDateStr(date); // Use local date helper
    return assignments
      .filter((a) => a.dateStr === dateStr)
      .map((assignment) => {
        const def = taskDefinitions.find((d) => d.id === assignment.taskId);
        if (!def) return null;
        return {
          id: def.id,
          assignmentId: assignment.id,
          text: def.text,
          completed: assignment.completed,
          duration: assignment.durationOverride || def.baselineDuration,
        };
      })
      .filter((t): t is Task => t !== null);
  };
  // Generate data for the current view
  const generateViewData = (): DayData[] => {
    const days: DayData[] = [];
    if (view === "week" || view === "stats") {
      const startOfWeek = new Date(currentDate);
      const day = startOfWeek.getDay();
      const diff = startOfWeek.getDate() - day + (day === 0 ? -6 : 1);
      startOfWeek.setDate(diff);
      for (let i = 0; i < 7; i++) {
        const date = new Date(startOfWeek);
        date.setDate(startOfWeek.getDate() + i);
        days.push(createDayData(date));
      }
    } else {
      const year = currentDate.getFullYear();
      const month = currentDate.getMonth();
      const firstDayOfMonth = new Date(year, month, 1);
      const startDay = firstDayOfMonth.getDay();
      const startDate = new Date(firstDayOfMonth);
      startDate.setDate(1 - startDay);
      for (let i = 0; i < 42; i++) {
        const date = new Date(startDate);
        date.setDate(startDate.getDate() + i);
        days.push(createDayData(date));
      }
    }
    return days;
  };
  const createDayData = (date: Date): DayData => {
    const isoDate = getLocalDateStr(date); // Use local date helper
    const tasks = getTasksForDate(date);
    const isDayOff = daysOff.includes(isoDate);
    const isComplete = tasks.length > 0 && tasks.every((t) => t.completed);
    return {
      id: isoDate,
      date: date.toLocaleDateString("en-US", {
        month: "short",
        day: "numeric",
      }),
      dayName: date.toLocaleDateString("en-US", {
        weekday: "long",
      }),
      fullDate: date,
      isToday: date.toDateString() === new Date().toDateString(),
      isDayOff,
      isComplete,
      tasks,
      hourLogs: [],
    };
  };
  const currentData = generateViewData();
  // Handlers
  const handleCreateTaskDef = (data: {
    text: string;
    description?: string;
    baselineDuration: number;
    isRecurring: boolean;
    recurringDays: number[];
  }) => {
    const newDef: TaskDefinition = {
      id: Math.random().toString(36).substr(2, 9),
      ...data,
    };
    setTaskDefinitions((prev) => [...prev, newDef]);
  };
  const handleUpdateTaskDef = (
    id: string,
    updates: Partial<TaskDefinition>,
  ) => {
    setTaskDefinitions((prev) =>
      prev.map((def) =>
        def.id === id
          ? {
              ...def,
              ...updates,
            }
          : def,
      ),
    );
  };
  const handleDeleteTaskDef = () => {
    if (!deletingTask) return;
    setTaskDefinitions((prev) => prev.filter((d) => d.id !== deletingTask.id));
    setAssignments((prev) => prev.filter((a) => a.taskId !== deletingTask.id));
    setDeletingTask(null);
  };
  const handleAssignTask = (dateStr: string, duration?: number) => {
    if (!assignModalTask) return;
    createAssignment(assignModalTask.id, dateStr, duration);
  };
  const createAssignment = (
    taskId: string,
    dateStr: string,
    duration?: number,
  ) => {
    const newAssignment: TaskAssignment = {
      id: Math.random().toString(36).substr(2, 9),
      taskId,
      dateStr,
      durationOverride: duration,
      completed: false,
    };
    setAssignments((prev) => [...prev, newAssignment]);
  };
  const handleDropTask = (taskId: string, dateStr: string) => {
    createAssignment(taskId, dateStr);
  };
  const handleRemoveTask = (assignmentId: string) => {
    setAssignments((prev) => prev.filter((a) => a.id !== assignmentId));
  };
  const handleToggleTask = (assignmentId: string) => {
    setAssignments((prev) =>
      prev.map((a) =>
        a.id === assignmentId
          ? {
              ...a,
              completed: !a.completed,
            }
          : a,
      ),
    );
  };
  const handleDurationChange = (assignmentId: string, duration: number) => {
    setAssignments((prev) =>
      prev.map((a) =>
        a.id === assignmentId
          ? {
              ...a,
              durationOverride: duration,
            }
          : a,
      ),
    );
  };
  const handleToggleDayOff = (dayId: string) => {
    setDaysOff((prev) =>
      prev.includes(dayId) ? prev.filter((d) => d !== dayId) : [...prev, dayId],
    );
  };
  const handleMarkTodayComplete = () => {
    const todayStr = getLocalDateStr(new Date()); // Use local date helper
    const todayAssignments = assignments.filter((a) => a.dateStr === todayStr);
    if (todayAssignments.length === 0) return;
    const allComplete = todayAssignments.every((a) => a.completed);
    setAssignments((prev) =>
      prev.map((a) => {
        if (a.dateStr !== todayStr) return a;
        return {
          ...a,
          completed: !allComplete,
        };
      }),
    );
  };
  // Selection Handlers
  const handleToggleSelectDay = (dayId: string) => {
    setSelectedDays((prev) =>
      prev.includes(dayId) ? prev.filter((d) => d !== dayId) : [...prev, dayId],
    );
  };
  const handleBulkComplete = () => {
    setAssignments((prev) =>
      prev.map((a) => {
        if (selectedDays.includes(a.dateStr)) {
          return {
            ...a,
            completed: true,
          };
        }
        return a;
      }),
    );
    setSelectedDays([]);
    setIsSelectMode(false);
  };
  // Navigation
  const handleNext = () => {
    const newDate = new Date(currentDate);
    if (view === "week") newDate.setDate(newDate.getDate() + 7);
    else if (view === "month") newDate.setMonth(newDate.getMonth() + 1);
    setCurrentDate(newDate);
  };
  const handlePrev = () => {
    const newDate = new Date(currentDate);
    if (view === "week") newDate.setDate(newDate.getDate() - 7);
    else if (view === "month") newDate.setMonth(newDate.getMonth() - 1);
    setCurrentDate(newDate);
  };
  const getHeaderText = () => {
    if (view === "week") {
      if (currentData.length === 0) return "";
      return `${currentData[0].date} - ${currentData[6].date}`;
    }
    return currentDate.toLocaleDateString("en-US", {
      month: "long",
      year: "numeric",
    });
  };
  const todayTasks = getTasksForDate(new Date());
  const isTodayComplete =
    todayTasks.length > 0 && todayTasks.every((t) => t.completed);
  return (
    <div className="min-h-screen bg-paper-texture p-4 md:p-8 font-serif-text overflow-x-hidden">
      <div className="max-w-7xl mx-auto transition-all duration-300">
        {/* Header */}
        <header className="mb-8 flex flex-col lg:flex-row justify-between items-center gap-6">
          <div className="flex items-center gap-3 w-full lg:w-auto">
            <div className="p-3 bg-[#2C2416] rounded-full text-[#FAF7F0] shadow-lg">
              <PenTool className="w-6 h-6" />
            </div>
            <div>
              <h1 className="font-handwriting text-5xl font-bold text-[#2C2416] leading-none">
                Weekly Journal
              </h1>
              <p className="text-[#8B7355] italic text-lg">
                Focus on what matters, one day at a time.
              </p>
            </div>
          </div>

          <div className="flex flex-col md:flex-row items-center gap-4 w-full lg:w-auto justify-end">
            <CompleteDayButton
              isComplete={isTodayComplete}
              onClick={handleMarkTodayComplete}
            />
            <div className="h-8 w-px bg-[#E8DCC4] hidden md:block" />
            <ViewToggle view={view} onChange={setView} />
          </div>
        </header>

        {/* Navigation Bar - Only show for week/month views */}
        {view !== "stats" && (
          <div className="flex justify-between items-center mb-6">
            <div className="flex items-center gap-6 bg-[#FFFBF5] px-6 py-3 rounded-full shadow-sm border border-[#E8DCC4] mx-auto md:mx-0">
              <button
                onClick={handlePrev}
                className="p-2 hover:bg-[#E8DCC4]/30 rounded-full text-[#2C2416]"
              >
                <ChevronLeft className="w-6 h-6" />
              </button>
              <span className="font-handwriting text-2xl font-bold min-w-[140px] text-center text-[#2C2416]">
                {getHeaderText()}
              </span>
              <button
                onClick={handleNext}
                className="p-2 hover:bg-[#E8DCC4]/30 rounded-full text-[#2C2416]"
              >
                <ChevronRight className="w-6 h-6" />
              </button>
            </div>
          </div>
        )}

        {/* Main Content */}
        <main className="relative min-h-[500px]">
          {view === "week" ? (
            <WeekView
              weekData={currentData}
              direction={0}
              onToggleTask={handleToggleTask}
              onToggleDayOff={handleToggleDayOff}
              onDurationChange={handleDurationChange}
              onDropTask={handleDropTask}
              onRemoveTask={handleRemoveTask}
            />
          ) : view === "month" ? (
            <MonthCalendar
              currentDate={currentDate}
              days={currentData}
              onDayClick={(id) => console.log("Clicked day", id)}
              selectedDays={selectedDays}
              onToggleSelectDay={handleToggleSelectDay}
              isSelectMode={isSelectMode}
              onToggleSelectMode={() => {
                setIsSelectMode(!isSelectMode);
                setSelectedDays([]);
              }}
            />
          ) : (
            <StatsPanel
              taskDefinitions={taskDefinitions}
              assignments={assignments}
              daysOff={daysOff}
            />
          )}
        </main>

        {/* Footer */}
        <footer className="mt-12 text-center opacity-60">
          <p className="font-handwriting text-2xl text-[#6B5D4F]">
            "The secret of your future is hidden in your daily routine."
          </p>
        </footer>
      </div>

      {/* Task Library Sidebar */}
      <TaskLibrary
        tasks={taskDefinitions}
        onAssign={(taskId) => {
          const task = taskDefinitions.find((t) => t.id === taskId);
          if (task) setAssignModalTask(task);
        }}
        onCreateTask={() => setIsCreateModalOpen(true)}
        onEditTask={setEditingTask}
        onDeleteTask={setDeletingTask}
      />

      {/* Bulk Actions Bar */}
      <BulkActionsBar
        selectedCount={selectedDays.length}
        onClearSelection={() => setSelectedDays([])}
        onMarkComplete={handleBulkComplete}
      />

      {/* Modals */}
      <TaskCreationModal
        isOpen={isCreateModalOpen}
        onClose={() => setIsCreateModalOpen(false)}
        onSave={handleCreateTaskDef}
      />

      {editingTask && (
        <TaskEditModal
          isOpen={!!editingTask}
          onClose={() => setEditingTask(null)}
          onSave={handleUpdateTaskDef}
          task={editingTask}
        />
      )}

      {deletingTask && (
        <DeleteConfirmModal
          isOpen={!!deletingTask}
          onClose={() => setDeletingTask(null)}
          onConfirm={handleDeleteTaskDef}
          taskName={deletingTask.text}
        />
      )}

      {assignModalTask && (
        <TaskAssignmentModal
          isOpen={!!assignModalTask}
          onClose={() => setAssignModalTask(null)}
          onAssign={handleAssignTask}
          taskName={assignModalTask.text}
          baselineDuration={assignModalTask.baselineDuration}
        />
      )}
    </div>
  );
}
