import { motion, AnimatePresence } from "framer-motion";
import { DayCard, DayData } from "./DayCard";
interface WeekViewProps {
  weekData: DayData[];
  direction: number;
  onToggleTask: (assignmentId: string) => void;
  onToggleDayOff: (dayId: string) => void;
  onDurationChange: (assignmentId: string, duration: number) => void;
  onDropTask: (taskId: string, dateStr: string) => void;
  onRemoveTask: (assignmentId: string) => void;
}
const variants = {
  enter: (direction: number) => ({
    x: direction > 0 ? 1000 : -1000,
    opacity: 0,
    rotateY: direction > 0 ? 45 : -45,
    scale: 0.95,
  }),
  center: {
    zIndex: 1,
    x: 0,
    opacity: 1,
    rotateY: 0,
    scale: 1,
  },
  exit: (direction: number) => ({
    zIndex: 0,
    x: direction < 0 ? 1000 : -1000,
    opacity: 0,
    rotateY: direction < 0 ? 45 : -45,
    scale: 0.95,
  }),
};
export function WeekView({
  weekData,
  direction,
  onToggleTask,
  onToggleDayOff,
  onDurationChange,
  onDropTask,
  onRemoveTask,
}: WeekViewProps) {
  return (
    <div className="relative w-full h-full perspective-1000">
      <AnimatePresence initial={false} custom={direction} mode="wait">
        <motion.div
          key={weekData[0].id}
          custom={direction}
          variants={variants}
          initial="enter"
          animate="center"
          exit="exit"
          transition={{
            x: {
              type: "spring",
              stiffness: 300,
              damping: 30,
            },
            opacity: {
              duration: 0.2,
            },
            rotateY: {
              duration: 0.4,
            },
          }}
          className="w-full h-full grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 p-2"
        >
          {weekData.map((day) => (
            <div key={day.id} className="h-[400px] md:h-[450px]">
              <DayCard
                day={day}
                onToggleTask={onToggleTask}
                onToggleDayOff={onToggleDayOff}
                onDurationChange={onDurationChange}
                onDropTask={onDropTask}
                onRemoveTask={onRemoveTask}
              />
            </div>
          ))}
        </motion.div>
      </AnimatePresence>
    </div>
  );
}
